// https://developer.edamam.com/

import "./App.css";
import Axios from "axios";
import { useState } from "react";
import RecipeTile from "./RecipeTile";
function App() {
  const [query, setQuery] = useState("");
  const [recipes, setRecipes] = useState([]);
  const [healthLabels, sethealthLabels] = useState("vegan");

  const YOUR_APP_ID = "3f1e7ff9";
  const YOUR_APP_KEY = "ca6b6e6eaf3a9e24317b4384625a766d";

  const url = `https://api.edamam.com/search?q=${query}&app_id=${YOUR_APP_ID}&app_key=${YOUR_APP_KEY}&&health=${healthLabels}`;

  async function getRecipes() {
    var result = await Axios.get(url);
    setRecipes(result.data.hits);
    console.log(result.data);
  }

  const onSubmit = (e) => {
    e.preventDefault();
    getRecipes();
  };

  return (
    <div className="App">
      <h1>Food Recipe Plaza</h1>
      <form className="app__searchForm" onSubmit={onSubmit}>
        <input
          className="app__input"
          type="text"
          placeholder="enter ingridient"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
        />
        <input className="app__submit" type="submit" value="Search" />
        <select className="app_healthLabels">
          <option value="vegan" onClick={() => sethealthLabels("vegan")}>
            Vegan
          </option>
          <option onClick={() => sethealthLabels("vegetarian")}>
            Vegetarian
          </option>
          <option onClick={() => sethealthLabels("paleo")}>Paleo</option>
        </select>
      </form>
      <div className="app__recipes">
        {recipes.map((recipe) => (
          <RecipeTile recipe={recipe} />
        ))}
      </div>
    </div>
  );
}

export default App;
